<template lang="pug">
.header
  .headerLeft(v-if="targetPerson.conversationID")
    //- TODO 點了開啟對方info
    .px-2.d-lg-none
      b-icon-chevron-left.mr-2.mt-2(@click="$emit('back')")
    .avatar(:style="targetPerson.stickerUrl")
      OnlineStatus(:status="targetPerson.isOnline")
    .name {{targetPerson.userName}}
    .infoIcon
  b-icon-x.ml-auto.headerRight.d-none(:class="{'d-lg-block': !isFullScreen}" @click="$emit('close')")
</template>

<script>

export default {
  components: {
    OnlineStatus: () => import('./OnlineStatus.vue'),
  },
  props: {
    targetPerson: {
      type: Object,
      default: () => {
        return {
          conversationID: '',
          userName: '',
          isOnline: false,
          stickerUrl: '',
        }
      }
    },
    isFullScreen: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {

    }
  }
}
</script>